import React, { Component } from "react";
import classes from './Quiz.module.css';
import ActiveQuiz from "../../components/ActiveQuiz/ActiveQuiz";
import FinishedQuiz from "../../components/FinishedQuiz/FinishedQuiz";

class Quiz extends React.Component {

    state = {

        results: {

        }, // {[answerId]: 'success' или 'error'}

        isFinished: false,
        activeQuestion: 0,
        answerState: null,  // {[id]: 'success' или 'error'}
        quiz: [


            {
                question: 'What year was Saint Petersburg founded?',
                rightAnswerID: 3,
                id: 1,
                answers: [
                    { text: '1813', id: 1 },
                    { text: '1755', id: 2 },
                    { text: '1703', id: 3 },
                    { text: '1801', id: 4 }
                ]
            },

            {
                question: 'What year was Moscow founded?',
                rightAnswerID: 1,
                id: 2,
                answers: [
                    { text: '1147', id: 1 },
                    { text: '1153', id: 2 },
                    { text: '2022', id: 3 },
                    { text: '1141', id: 4 }
                ]
            },

            {
                question: 'What year was Novgorod founded?',
                rightAnswerID: 3,
                id: 3,
                answers: [
                    { text: '812', id: 1 },
                    { text: '911', id: 2 },
                    { text: '859', id: 3 },
                    { text: '1007', id: 4 }
                ]
            },

            {
                question: 'What year was Vladivostok founded?',
                rightAnswerID: 2,
                id: 4,
                answers: [
                    { text: '1812', id: 1 },
                    { text: '1860', id: 2 },
                    { text: '1799', id: 3 },
                    { text: '1828', id: 4 }
                ]
            }


        ]
    }

    onAnswerClickHandler = (answerId) => {

        if (this.state.answerState) {

            const key = Object.keys(this.state.answerState)[0]

            if (this.state.answerState[key] === 'success') {
                return
            }

        }

        const question = this.state.quiz[this.state.activeQuestion]

        const results = this.state.results

        if (question.rightAnswerID === answerId) {

            if (!results[question.id]) {

                results[question.id] = 'success'

            }

            this.setState({
                answerState: {[answerId]: 'success'},
                results: results
            })

            const timeout = window.setTimeout(() => {

                if (this.isQuizFinished()) {

                    this.setState(

                        {
                            isFinished: true,
                            //  меняет значение isFinished на true в нашем state
                        })

                }

                else {


                    this.setState(
                        {
                            activeQuestion: this.state.activeQuestion + 1,
                            answerState: null

                        }

                    )


                }

                window.clearTimeout(timeout)

            }, 500)



        }

        else {

            results[question.id] = 'error'

            this.setState({
                answerState: { [answerId]: 'error' },
                results: results
            })


        }


    }

    isQuizFinished() {

        return this.state.activeQuestion + 1 === this.state.quiz.length

        // проверка, закончен квиз или нет - возвращает true или false в зависимости от того, является ли текущий ответ заключительным
    }

    retryHandler = () => {

        this.setState({

            activeQuestion: 0,
            answerState: null,
            isFinished: false,
            results: {}
        })
    }

    render() {


        return (

            <div className={classes.Quiz}>


                <div className={classes.QuizWrapper}>
                    <h1>Please answer all the questions</h1>

                    {
                        // если тест закончен (isFinished равно true), то показать надпись Finished
                        // если нет - продолжать показывать наш ActiveQuiz со списком вопросов

                        this.state.isFinished

                            ? <FinishedQuiz

                                results={this.state.results}
                                quiz={this.state.quiz}
                                onRetry={this.retryHandler}


                            />

                            : <ActiveQuiz
                                answers={this.state.quiz[this.state.activeQuestion].answers}
                                question={this.state.quiz[this.state.activeQuestion].question}
                                onAnswerClick={this.onAnswerClickHandler}
                                quizLength={this.state.quiz.length}
                                answerNumber={this.state.activeQuestion + 1}
                                state={this.state.answerState}
                            />

                    }


                </div>
            </div>

        )
    }
}

export default Quiz;